<template>
    <div>
        <loading :active.sync="isLoading"></loading>
        <div class="text-right mt-4">
            <button class="btn btn-primary" @click="openModal(true)">建立新的產品</button>
        </div>
        <table class="table mt-4">
            <thead>
                <tr>
                    <th width="120">分類</th>
                    <th>產品名稱</th>
                    <th width="120">原價</th>
                    <th width="120">售價</th>
                    <th width="100">是否啟用</th>
                    <th width="120">編輯</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="item in products" :key="item.id">
                    <td>{{item.category}}</td>
                    <td>{{item.title}}</td>
                    <td class="text-right">{{item.origin_price | currency}}</td>
                    <td class="text-right">{{item.price | currency}}</td>
                    <td>
                        <span v-if="item.is_enabled" class="text-success">啟用</span>
                        <span v-else>未啟用</span>
                    </td>
                    <td>
                        <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)">編輯</button>
                        <button class="btn btn-outline-danger btn-sm"  @click="openDelProductModal(item)">刪除</button>
                    </td>
                </tr>
            </tbody>
        </table>

        <!-- <nav aria-label="Page navigation example">
        <ul class="pagination">
            <li class="page-item" :class="{'disabled': !pagination.has_pre}">
            <a class="page-link" href="#" aria-label="Previous" @click.prevent="getProducts(pagination.current_page -1)">
                <span aria-hidden="true">&laquo;</span>
            </a>
            </li>
            <li class="page-item" v-for="page in pagination.total_pages" :key="page" :class="{'active':pagination.current_page === page }">
                <a class="page-link" href="#" @click.prevent="getProducts(page)">{{page}}</a></li>
            
            <li class="page-item" :class="{'disabled': !pagination.has_next}" > 
            <a class="page-link" href="#" aria-label="Next" @click.prevent="getProducts(pagination.current_page + 1)">
                <span aria-hidden="true">&raquo;</span>
            </a>
            </li>
        </ul>
        </nav> -->


        <!-- 將pagination元件化 -->
        <pagination :pages="pagination" @emitPages="getProducts"></pagination>

        <!-- <pagination></pagination> -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
        aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content border-0">
                    <div class="modal-header bg-dark text-white">
                        <h5 class="modal-title" id="exampleModalLabel">
                        <span v-if="isNew">新增產品</span>
                        <span v-else>編輯：{{temProduct.title}} </span>
                        </h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                        <div class="col-sm-4">
                            <div class="form-group">
                            <label for="image">輸入圖片網址</label>
                            <input type="text" class="form-control" id="image"
                                v-model="temProduct.imageUrl"
                                placeholder="請輸入圖片連結">
                            </div>
                            <div class="form-group">
                            <label for="customFile">或 上傳圖片
                                <i class="fas fa-spinner fa-spin" v-if=" status.fileUploading"></i>
                            </label>
                            <input type="file" id="customFile" class="form-control"
                                ref="files" @change="uploadFile">
                            </div>
                            <img img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                            class="img-fluid" :src="temProduct.imageUrl" alt="">
                        </div>
                        <div class="col-sm-8">
                            <div class="form-group">
                            <label for="title">標題</label>
                            <input type="text" class="form-control" id="title"
                                v-model="temProduct.title"
                                placeholder="請輸入標題">
                            </div>

                            <div class="form-row">
                            <div class="form-group col-md-6">
                                <label for="category">分類</label>
                                <input type="text" class="form-control" id="category"
                                v-model="temProduct.category"
                                placeholder="請輸入分類">
                            </div>
                            <div class="form-group col-md-6">
                                <label for="price">單位</label>
                                <input type="unit" class="form-control" id="unit"
                                v-model="temProduct.unit"
                                placeholder="請輸入單位">
                            </div>
                            </div>

                            <div class="form-row">
                            <div class="form-group col-md-6">
                            <label for="origin_price">原價</label>
                                <input type="number" class="form-control" id="origin_price"
                                v-model="temProduct.origin_price"
                                placeholder="請輸入原價">
                            </div>
                            <div class="form-group col-md-6">
                                <label for="price">售價</label>
                                <input type="number" class="form-control" id="price"
                                v-model="temProduct.price"
                                placeholder="請輸入售價">
                            </div>
                            </div>
                            <hr>

                            <div class="form-group">
                            <label for="description">產品描述</label>
                            <textarea type="text" class="form-control" id="description"
                                v-model="temProduct.description"
                                placeholder="請輸入產品描述"></textarea>
                            </div>
                            <div class="form-group">
                            <label for="content">說明內容</label>
                            <textarea type="text" class="form-control" id="content"
                                v-model="temProduct.content"
                                placeholder="請輸入產品說明內容"></textarea>
                            </div>
                            <div class="form-group">
                            <div class="form-check">
                                <input class="form-check-input" type="checkbox"
                                v-model="temProduct.is_enabled"
                                :true-value="1"
                                :flase-value="0"
                                id="is_enabled">
                                <label class="form-check-label" for="is_enabled">
                                是否啟用
                                </label>
                            </div>
                            </div>
                        </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                        <button type="button" class="btn btn-primary" @click="updataProduct">確認</button>
                    </div>
                </div>
            </div>
        </div>

        <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
            aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>刪除產品</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    是否刪除 <strong class="text-danger">{{ temProduct.title }}</strong> 商品(刪除後將無法恢復)。
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-danger" @click="delProduct"
                    >確認刪除</button>
                </div>
            </div>
        </div>
</div>


        
        

    </div>
    
</template>





<script>
import $ from 'jquery';
import Pagination from '../Pagination';




export default {
    components: {
        Pagination
        
    },
    data(){
        return{
            products:[],
            pagination:{},
            temProduct:{},
            isNew:false,
            isLoading:false,
            status:{
                fileUploading:false,
            }
        };
    },
    

    methods:{
        getProducts(page=1){
            const vm = this;
             const api =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`;  
             vm.isLoading = true;  
            this.$http.get(api).then((response) => {
                // console.log(response.data)
                vm.products = response.data.products;
                vm.isLoading = false;
                vm.pagination = response.data.pagination;
                // console.log(vm.products )
            })
        },
        openModal(isNew,item){
            $('#productModal').modal('show');
            const vm = this;
            if(isNew){
                vm.temProduct={};
                vm.isNew=true;
            }else{
                vm.temProduct= Object.assign({},item) ; //避免有參考的特性
                vm.isNew=false;
            }
        },        
        updataProduct(){
            const vm = this;
            let api =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;  
            let httpMetod = 'post'
            if(!vm.isNew){
                api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.temProduct.id}`
                httpMetod = 'put'
            }

            this.$http[httpMetod](api,{data:vm.temProduct}).then((response) => {
                 console.log(response.data);
                 if(response.data.success){
                     $('#productModal').modal('hide');
                     vm.getProducts();
                     this.$bus.$emit('message:push',response.data.message,'success');
                 }else{
                     $('#productModal').modal('hide');
                     vm.getProducts();
                     this.$bus.$emit('message:push',response.data.message,'danger');
                     console.log('新增失敗');
                 }
                // vm.products = response.data.products;
                // console.log(vm.products )
            })
        },
        openDelProductModal(item){
            const vm = this;
            $('#delProductModal').modal('show');
            vm.temProduct = Object.assign({}, item);
        },
        delProduct() {
            const vm = this;
            const api =`${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.temProduct.id}`;
            this.$http.delete(api).then((response) => {
                console.log(response, vm.temProduct);
                $('#delProductModal').modal('hide');
                // vm.isLoading = false;
                this.getProducts();
            });
        },
        uploadFile(){
            // console.log(this )
            const uploadedFile = this.$refs.files.files[0];
            const vm = this;
            const formData = new FormData();
            formData.append('file-to-upload',uploadedFile);
            const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`
            vm.status.fileUploading = true;
            this.$http.post(url,formData,{
                headers:{
                    'Content-Type':'multipart/form-data'
                }
            }).then((response) =>{  
                vm.status.fileUploading = false;              
                if(response.data.success){   
                    //強制綁定 達成雙向綁定                 
                    vm.$set(vm.temProduct,'imageUrl',response.data.imageUrl)
                    
                }else{
                    this.$bus.$emit('message:push',response.data.message,'danger');
                }
            })
        }
    },
    created(){
        this.getProducts();
        
    }
}
</script>
    
   


